ex;\n  align-items: center;\n}\n\n.search-icon {\n  position: absolute;\n  left: 16px;\n  color: var(--text-muted);\n  z-index: 1;\n}\n\n.search-input {\n  width: 100%;\n  padding: 14px 16px 14px 48px;\n  border: none;\n  background: none;\n  font-size: 16px;\n  color: var(--text-primary);\n  border-radius: var(--radius-md);\n}\n\n.search-input::placeholder {\n  color: var(--text-muted);\n}\n\n.search-input:focus {\n  outline: none;\n}\n\n.filter-toggle {\n  background: none;\n  border: none;\n  padding: 12px;\n  color: var(--text-secondary);\n  cursor: pointer;\n  border-radius: var(--radius-sm);\n  transition: all 0.2s ease;\n}\n\n.filter-toggle:hover {\n  background: var(--background-light);\n  color: var(--accent-color);\n}\n\n.search-button {\n  background: var(--accent-color);\n  color: white;\n  border: none;\n  padding: 14px 28px;\n  border-radius: var(--radius-md);\n  font-weight: 500;\n  cursor: pointer;\n  transition: all 0.2s ease;\n}\n\n.search-button:hover {\n  background: var(--accent-hover);\n}\n\n/* Quick Filters */\n.quick-filters {\n  background: white;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-lg);\n  padding: 20px;\n  margin-top: 12px;\n  box-shadow: var(--shadow-light);\n}\n\n.quick-filters-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 16px;\n  font-weight: 500;\n}\n\n.clear-filters {\n  background: none;\n  border: none;\n  color: var(--accent-color);\n  cursor: pointer;\n  font-size: 14px;\n}\n\n.quick-filters-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));\n  gap: 16px;\n}\n\n.filter-group {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.filter-group label {\n  font-weight: 500;\n  font-size: 14px;\n  color: var(--text-primary);\n}\n\n.filter-group select,\n.filter-group input {\n  padding: 8px 12px;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-sm);\n  font-size: 14px;\n}\n\n.filter-group select:focus,\n.filter-group input:focus {\n  border-color: var(--accent-color);\n  outline: none;\n}\n\n/* Category Card Styles */\n.category-card {\n  background: white;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-xl);\n  padding: 32px 24px;\n  text-align: center;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  position: relative;\n  overflow: hidden;\n}\n\n.category-card:hover {\n  transform: translateY(-4px);\n  box-shadow: var(--shadow-medium);\n  border-color: var(--accent-color);\n}\n\n.category-card:hover .category-arrow {\n  transform: translateX(4px);\n  color: var(--accent-color);\n}\n\n.category-icon {\n  width: 64px;\n  height: 64px;\n  background: linear-gradient(135deg, var(--accent-color), var(--accent-hover));\n  border-radius: var(--radius-lg);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  margin: 0 auto 20px;\n  color: white;\n}\n\n.category-icon [data-feather] {\n  width: 32px;\n  height: 32px;\n}\n\n.category-name {\n  font-size: 20px;\n  font-weight: 600;\n  color: var(--text-primary);\n  margin-bottom: 8px;\n}\n\n.category-description {\n  color: var(--text-secondary);\n  font-size: 14px;\n}\n\n.category-arrow {\n  position: absolute;\n  top: 20px;\n  right: 20px;\n  color: var(--text-muted);\n  transition: all 0.3s ease;\n}\n\n/* Product Card Styles */\n.product-card {\n  background: white;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-xl);\n  overflow: hidden;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  position: relative;\n}\n\n.product-card:hover {\n  transform: translateY(-4px);\n  box-shadow: var(--shadow-medium);\n  border-color: var(--accent-color);\n}\n\n.product-card:hover .product-overlay {\n  opacity: 1;\n}\n\n.product-card:hover .card-hover-indicator {\n  opacity: 1;\n  transform: translateX(0);\n}\n\n.product-image {\n  position: relative;\n  height: 200px;\n  background: var(--background-light);\n  overflow: hidden;\n}\n\n.product-image img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n}\n\n.image-placeholder {\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  color: var(--text-muted);\n  gap: 8px;\n}\n\n.image-placeholder [data-feather] {\n  width: 32px;\n  height: 32px;\n}\n\n.product-overlay {\n  position: absolute;\n  top: 0;\n  right: 0;\n  background: linear-gradient(135deg, rgba(229, 199, 126, 0.9), rgba(212, 181, 108, 0.9));\n  padding: 12px;\n  opacity: 0;\n  transition: opacity 0.3s ease;\n}\n\n.quick-action-button {\n  background: white;\n  border: none;\n  padding: 8px;\n  border-radius: var(--radius-sm);\n  color: var(--accent-color);\n  cursor: pointer;\n  box-shadow: var(--shadow-light);\n}\n\n.product-info {\n  padding: 20px;\n}\n\n.product-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: flex-start;\n  margin-bottom: 12px;\n}\n\n.product-name {\n  font-size: 18px;\n  font-weight: 600;\n  color: var(--text-primary);\n  margin: 0;\n}\n\n.product-price {\n  font-size: 16px;\n  font-weight: 600;\n  color: var(--accent-color);\n}\n\n.product-details {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n  margin-bottom: 16px;\n}\n\n.product-brand {\n  font-size: 14px;\n  color: var(--text-secondary);\n}\n\n.color-info {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n}\n\n.color-name {\n  font-size: 14px;\n  color: var(--text-secondary);\n}\n\n.color-swatch {\n  width: 16px;\n  height: 16px;\n  border-radius: 50%;\n  border: 2px solid white;\n  box-shadow: 0 0 0 1px var(--border-color);\n}\n\n.product-tags {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n  margin-bottom: 16px;\n}\n\n.tag {\n  padding: 4px 8px;\n  border-radius: var(--radius-sm);\n  font-size: 12px;\n  font-weight: 500;\n}\n\n.category-tag {\n  background: var(--accent-color);\n  color: white;\n}\n\n.occasion-tag {\n  background: var(--background-light);\n  color: var(--text-secondary);\n}\n\n.pattern-tag {\n  background: #e3f2fd;\n  color: #1976d2;\n}\n\n.product-actions {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.card-hover-indicator {\n  position: absolute;\n  top: 50%;\n  right: 20px;\n  transform: translateY(-50%) translateX(20px);\n  opacity: 0;\n  transition: all 0.3s ease;\n  color: var(--accent-color);\n}\n\n/* Bundle Card Styles */\n.bundle-card {\n  background: white;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-xl);\n  overflow: hidden;\n  transition: all 0.3s ease;\n  position: relative;\n}\n\n.bundle-card:hover {\n  transform: translateY(-4px);\n  box-shadow: var(--shadow-medium);\n  border-color: var(--accent-color);\n}\n\n.bundle-card:hover .bundle-overlay {\n  opacity: 1;\n}\n\n.bundle-image {\n  position: relative;\n  height: 240px;\n  background: var(--background-light);\n  overflow: hidden;\n}\n\n.bundle-image img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n}\n\n.bundle-overlay {\n  position: absolute;\n  top: 12px;\n  right: 12px;\n  display: flex;\n  gap: 8px;\n  opacity: 0;\n  transition: opacity 0.3s ease;\n}\n\n.action-button {\n  background: rgba(255, 255, 255, 0.9);\n  border: none;\n  padding: 8px;\n  border-radius: var(--radius-sm);\n  cursor: pointer;\n  color: var(--text-secondary);\n  transition: all 0.2s ease;\n  box-shadow: var(--shadow-light);\n}\n\n.action-button:hover {\n  background: white;\n  color: var(--accent-color);\n}\n\n.action-button.saved {\n  color: #e91e63;\n}\n\n.bundle-info {\n  padding: 24px;\n}\n\n.bundle-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: flex-start;\n  margin-bottom: 12px;\n}\n\n.bundle-title {\n  font-size: 18px;\n  font-weight: 600;\n  color: var(--text-primary);\n  margin: 0;\n}\n\n.bundle-price {\n  font-size: 16px;\n  font-weight: 600;\n  color: var(--accent-color);\n}\n\n.bundle-description {\n  color: var(--text-secondary);\n  font-size: 14px;\n  line-height: 1.5;\n  margin-bottom: 16px;\n}\n\n.bundle-items {\n  margin-bottom: 16px;\n}\n\n.items-preview {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.item-preview {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  font-size: 13px;\n}\n\n.item-name {\n  color: var(--text-secondary);\n}\n\n.item-price {\n  color: var(--text-primary);\n  font-weight: 500;\n}\n\n.more-items {\n  font-size: 12px;\n  color: var(--text-muted);\n  font-style: italic;\n}\n\n.bundle-tags {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 6px;\n  margin-bottom: 16px;\n}\n\n.bundle-tags .tag {\n  font-size: 11px;\n}\n\n.occasion-tag {\n  background: var(--accent-color);\n  color: white;\n}\n\n.style-tag {\n  background: #f3e5f5;\n  color: #7b1fa2;\n}\n\n.season-tag {\n  background: #e8f5e8;\n  color: #388e3c;\n}\n\n.bundle-score {\n  margin-bottom: 16px;\n}\n\n.score-bar {\n  width: 100%;\n  height: 4px;\n  background: var(--border-color);\n  border-radius: 2px;\n  overflow: hidden;\n  margin-bottom: 4px;\n}\n\n.score-fill {\n  height: 100%;\n  background: var(--accent-color);\n  transition: width 0.3s ease;\n}\n\n.score-text {\n  font-size: 12px;\n  color: var(--text-secondary);\n}\n\n.bundle-actions {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.action-group {\n  display: flex;\n  gap: 8px;\n}\n\n/* Filter Panel Styles */\n.filter-panel {\n  background: white;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-lg);\n  padding: 20px;\n  height: fit-content;\n  position: sticky;\n  top: 100px;\n}\n\n.filter-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 20px;\n  padding-bottom: 12px;\n  border-bottom: 1px solid var(--border-color);\n}\n\n.filter-header h3 {\n  font-size: 18px;\n  font-weight: 600;\n  color: var(--text-primary);\n  margin: 0;\n}\n\n.filter-header-actions {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n}\n\n.clear-all {\n  background: none;\n  border: none;\n  color: var(--accent-color);\n  cursor: pointer;\n  font-size: 13px;\n  font-weight: 500;\n}\n\n.collapse-toggle {\n  background: none;\n  border: none;\n  color: var(--text-secondary);\n  cursor: pointer;\n  padding: 4px;\n  border-radius: var(--radius-sm);\n}\n\n.collapse-toggle:hover {\n  background: var(--background-light);\n}\n\n.filter-content {\n  max-height: none;\n  overflow: visible;\n  transition: all 0.3s ease;\n}\n\n.filter-content.collapsed {\n  max-height: 0;\n  overflow: hidden;\n}\n\n.filter-group {\n  margin-bottom: 24px;\n}\n\n.filter-label {\n  display: block;\n  font-weight: 500;\n  font-size: 14px;\n  color: var(--text-primary);\n  margin-bottom: 8px;\n}\n\n.filter-input,\n.filter-select {\n  width: 100%;\n  padding: 10px 12px;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-sm);\n  font-size: 14px;\n  background: white;\n}\n\n.filter-input:focus,\n.filter-select:focus {\n  border-color: var(--accent-color);\n  outline: none;\n}\n\n.price-range {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n}\n\n.price-input {\n  flex: 1;\n  padding: 10px 12px;\n  border: 1px solid var(--border-color);\n  border-radius: var(--radius-sm);\n  font-size: 14px;\n}\n\n.price-separator {\n  color: var(--text-secondary);\n  font-size: 14px;\n}\n\n.color-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));\n  gap: 12px;\n}\n\n.color-option {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 6px;\n  cursor: pointer;\n  padding: 8px;\n  border-radius: var(--radius-sm);\n  transition: background-color 0.2s ease;\n}\n\n.color-option:hover {\n  background: var(--background-light);\n}\n\n.color-option input[type=\"radio\"] {\n  display: none;\n}\n\n.color-swatch {\n  width: 24px;\n  height: 24px;\n  border-radius: 50%;\n  border: 2px solid white;\n  box-shadow: 0 0 0 1px var(--border-color);\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.any-color {\n  background: linear-gradient(45deg, #ccc 25%, transparent 25%), linear-gradient(-45deg, #ccc 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #ccc 75%), linear-gradient(-45deg, transparent 75%, #ccc 75%);\n  background-size: 4px 4px;\n  background-position: 0 0, 0 2px, 2px -2px, -2px 0px;\n}\n\n.any-color span {\n  font-size: 8px;\n  color: var(--text-secondary);\n  font-weight: 500;\n}\n\n.color-check {\n  width: 12px;\n  height: 12px;\n  color: white;\n}\n\n.color-label {\n  font-size: 11px;\n  color: var(--text-secondary);\n  text-align: center;\n}\n\n.active-filters {\n  border-top: 1px solid var(--border-color);\n  padding-top: 16px;\n  margin-top: 16px;\n}\n\n.active-filters h4 {\n  font-size: 14px;\n  color: var(--text-primary);\n  margin-bottom: 8px;\n}\n\n.filter-tags {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 6px;\n}\n\n.filter-tag {\n  background: var(--background-light);\n  color: var(--text-primary);\n  padding: 4px 8px;\n  border-radius: var(--radius-sm);\n  font-size: 12px;\n  display: inline-flex;\n  align-items: center;\n  gap: 4px;\n}\n\n.remove-filter {\n  background: none;\n  border: none;\n  color: var(--text-muted);\n  cursor: pointer;\n  padding: 0;\n  display: inline-flex;\n  align-items: center;\n}\n\n.remove-filter:hover {\n  color: var(--accent-color);\n}\n\n/* Search Page Styles */\n.search-page {\n  padding-bottom: 40px;\n}\n\n.search-header {\n  margin-bottom: 32px;\n  max-width: 800px;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.results-header {\n  margin-bottom: 32px;\n  text-align: center;\n}\n\n.results-title {\n  font-size: 28px;\n  font-weight: 600;\n  color: var(--text-primary);\n  margin-bottom: 8px;\n}\n\n.search-query,\n.category-filter {\n  font-size: 16px;\n  color: var(--text-secondary);\n  margin-bottom: 4px;\n}\n\n.search-content {\n  display: grid;\n  grid-template-columns: 280px 1fr;\n  gap: 32px;\n  align-items: start;\n}\n\n.filters-sidebar {\n  position: sticky;\n  top: 100px;\n}\n\n.results-section {\n  min-height: 400px;\n}\n\n/* Product Detail Page Styles */\n.product-detail-page {\n  padding-bottom: 40px;\n}\n\n.product-detail {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  gap: 48px;\n  margin-top: 32px;\n}\n\n.product-image-section {\n  position: sticky;\n  top: 100px;\n  height: fit-content;\n}\n\n.product-image-section .product-image {\n  height: 500px;\n  border-radius: var(--radius-xl);\n  overflow: hidden;\n  background: var(--background-light);\n}\n\n.product-info-section {\n  padding: 20px 0;\n}\n\n.product-header {\n  margin-bottom: 32px;\n}\n\n.product-title {\n  font-size: 32px;\n  font-weight: 700;\n  color: var(--text-primary);\n  margin-bottom: 12px;\n}\n\n.category-badge {\n  background: var(--accent-color);\n  color: white;\n  padding: 6px 12px;\n  border-radius: var(--radius-sm);\n  font-size: 14px;\n  font-weight: 500;\n}\n\n.product-details {\n  margin-bottom: 40px;\n}\n\n.detail-group {\n  margin-bottom: 32px;\n}\n\n.detail-group h3 {\n  font-size: 18px;\n  font-weight: 600;\n  color: var(--text-primary);\n  margin-bottom: 16px;\n}\n\n.detail-grid {\n  display: grid;\n  gap: 12px;\n}\n\n.detail-item {\n  display: grid;\n  grid-template-columns: 120px 1fr;\n  align-items: center;\n  padding: 8px 0;\n  border-bottom: 1px solid var(--border-color);\n}\n\n.detail-label {\n  font-weight: 500;\n  color: var(--text-secondary);\n  font-size: 14px;\n}\n\n.detail-value {\n  color: var(--text-primary);\n  font-size: 14px;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n}\n\n.detail-value.price {\n  font-size: 18px;\n  font-weight: 600;\n  color: var(--accent-color);\n}\n\n.product-description {\n  line-height: 1.6;\n  color: var(--text-secondary);\n}\n\n.product-actions {\n  display: flex;\n  gap: 16px;\n  flex-wrap: wrap;\n}\n\n/* Recommendation Page Styles */\n.recommendation-page {\n  padding-bottom: 40px;\n}\n\n.